.footer {
  width: 100%;
  background-color: rgb(24, 24, 24);
  padding: 25px 0;
  color: #fff;
}

.footer-container {
  display: flex;
  flex-direction: column;
  gap: 50px;
}

.footer-links-block {
  display: flex;
  gap: 60px;
}

.link-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.link-group__title {
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 0.1px;
  line-height: 1.4;
}

.footer-link {
  font-size: 16px;
  font-weight: 500;
  letter-spacing: 0.1px;
  line-height: 1.4;
  color: #fff;
  opacity: 0.7;
  transition: 0.25s;

  &:hover{
    color: var(--second-color-hover);
  }
}

.footer__down-block {
  display: flex;
  flex-direction: column;
}

.footer-stats {
  display: flex;
  gap: 60px;
  padding-bottom: 25px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.stat-title {
  font-weight: 400;
  font-size: 26px;
  line-height: 1.4;
}

.stat-description {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.4;
  color: #fff;
  opacity: 0.7;
}

.footer__down-block-links {
  display: flex;
  gap: 16px;
  padding: 25px 0;
  align-items: center;
}

.footer-logo {
  width: 200px;

  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}


.footer__down-block-link {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.4;
  color: #fff;
  opacity: 0.7;
  transition: 0.25s;

  &:hover{
    color: var(--second-color-hover);
  }
}

.social-links {
  margin-left: auto;

  .social {
    list-style: none;
    display: flex;
    gap: 5px;

    .social__link {
      background-color: rgba(196, 196, 196, 0.2);
      padding: 5px;
      border-radius: 5px;
      display: flex;
      width: 32px;
      height: 32px;
      opacity: 0.7;
      transition: 0.25s;

      &:hover{
        opacity: 1;
      }

      img {
        height: 100%;
        width: 100%;
        object-fit: contain;
      }
    }
  }
}

.footer-copyright{
  display: flex;
  flex-direction: column;
  gap: 2px;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.4;
  color: rgb(92, 99, 112);
}

@media (max-width:768px) {
  .footer-links-block{
    flex-wrap: wrap;
    gap: 30px 60px;
  }
  .footer-stats{
    flex-wrap: wrap;
  }

}

@media (max-width:640px){
  .footer__down-block-links{
    flex-direction: column;
    align-items: flex-start;
  }
  .social-links{
    margin-left: unset;
  }
}

@media (max-width:475px) {
  .footer-links-block{
    flex-direction: column;
  }
}
