.header {
  width: 100%;
  position: relative;
}

.header__head {
  display: flex;
  flex-direction: column;
}

.head__links {
  display: flex;
  gap: 20px;
  padding: 12px 0;
}

.head__link {
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  transition: 0.25s;
  color: #000;

  &:hover {
    color: var(--second-color);
  }
}

.header__head-border-bottom {
  width: 200%;
  transform: translateX(-25%);
  border-bottom: 1px solid rgb(243, 243, 247);
}

.header__container {
  display: flex;
  flex-direction: column;
  padding-bottom: 25px;
  gap: 16px;
}

.header__main-block {
  display: flex;
  gap: 40px;
  align-items: center;
}

.header__logo {
  width: 250px;
}

.header__delivery-text {
  font-size: 18px;
  line-height: 1.2;
  font-weight: 500;

  .select-sity {
    color: var(--second-color);
    transition: 0.25s;

    &:hover {
      color: var(--second-color-hover);
    }
  }
}

.header__phone-number {
  font-size: 18px;
  line-height: 1.2;
  font-weight: 500;
  color: #000;
  transition: 0.25s;

  &:hover {
    color: var(--second-color);
  }
}

.mobile__header {
  display: none;
  z-index: 25;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  transition: background-color 0.25s;
  padding: 12px 0;

  &.header--active {
    background-color: #000;
    padding: 12px var(--container-offset);
    margin: 0 calc(var(--container-offset) * -1);
  }
  &.select-sity-menu__active{
    background-color: rgb(27, 27, 27)
  }
}

.header__logo-mobile {
  width: 200px;
}

.mobile__phone-number,
.mobile__delivery,
.mobile-delivery-time,
.google-play__img,
.mobile__select-sity-menu {
  display: none;
}

.star-rating {
  display: inline-flex;
  direction: ltr;
  user-select: none;
}

.star {
  width: 16px;
  height: 16px;
  cursor: pointer;
  background: linear-gradient(to right, #fc0 0%, #fc0 100%, #ccc 100%, #ccc 100%);
  -webkit-mask: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><polygon points="12,2 15,9 22,9 17,14 19,21 12,17 5,21 7,14 2,9 9,9"/></svg>') center / contain no-repeat;
  mask: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><polygon points="12,2 15,9 22,9 17,14 19,21 12,17 5,21 7,14 2,9 9,9"/></svg>') center / contain no-repeat;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  transition: background 0.3s;
}

.delivery-time__text {
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
}

.delivery-time__rate {
  font-size: 18px;
  line-height: 20px;
  font-weight: 500;
  color: yellow;
}



.mobile__menu {
  display: contents;
  transform: translateY(-200%);
  transition: 0.25s;

}

.burger {
  --burger-width: 24px;
  --burger-height: 24px;
  --burger-line-height: 2px;

  position: relative;
  border: none;
  padding: 0;
  width: var(--burger-width);
  height: var(--burger-height);
  color: #000;
  background-color: transparent;
  margin-left: auto;
  cursor: pointer;

  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: var(--burger-line-height);
    background-color: currentColor;
    transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
  }

  &::before {
    top: 0;
  }

  &::after {
    top: calc(100% - var(--burger-line-height));
  }

  &__line {
    position: absolute;
    left: 0;
    top: 50%;
    width: 100%;
    height: var(--burger-line-height);
    background-color: currentColor;
    transform: translateY(-50%);
    transition: transform 0.3s ease-in-out;
  }

  &--active {
    color: #fff;

    &::before {
      top: 50%;
      transform: rotate(45deg);
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

    &::after {
      top: 50%;
      transform: rotate(-45deg);
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

    .burger__line {
      transform: scale(0);
      transition: transform 0.3s ease-in-out;
    }
  }
}


.header__order {
  color: #fff;
  padding: 8px 20px;
  font-size: 16px;
  line-height: 24px;
  background-color: var(--second-color);
  transition: 0.25s;
  border-radius: 100px;
  margin-left: auto;

  &:hover {
    background-color: var(--second-color-hover);
  }
}

.nav-logo {
  width: 0;
  transition: 0.25s;
}

.header-fixed {
  width: 100%;
}

.fixed {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 95;
  backdrop-filter: blur(10px);
  box-shadow: rgba(0, 0, 0, 0.1) 0px 2px 4px 0px;
  display: flex;
  justify-content: center;

  .nav-logo {
    width: 32px;
  }

  .nav__container {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 0 var(--container-offset);
    max-width: var(--container-width);
  }
}

.sticky-placeholder {
  height: 0;
  visibility: hidden;
}

.nav-menu-line {
  width: 100%;
  display: flex;
  gap: 20px;
  align-items: center;
}

.nav-list {
  list-style: none;
  display: flex;
  gap: 20px;
  overflow-x: scroll;
  padding: 10px;
  align-items: center;
  scrollbar-width: none;
}

.nav-list__link {
  font-weight: 600;
  color: #000;
  font-size: 14px;
  transition: 0.25s;

  &:hover {
    color: var(--second-color);
  }
}

.storyboard {
  width: 100%;
  position: relative;
}

.swiper__storyboard {
  width: 100%;
  height: 100%;
  overflow: visible;
}

.storyboard__slide {
  min-width: 200px;
  width: auto;
  border-radius: 20px;
  overflow: hidden;
  cursor: pointer;
}

.storyboard-bg {
  width: 100%;
  aspect-ratio: 4/5;
  position: relative;

  img {
    position: absolute;
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center center;
  }
}

.storyboard-btn {
  position: absolute;
  top: 50%;
  height: 48px;
  width: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;

  svg {
    height: 24px;
    width: 14px;
    object-fit: contain;

    use {
      fill: none;
      stroke: var(--second-color);
      stroke-linecap: round;
      stroke-width: 2px;
      stroke-linejoin: round;
      transition: 0.25s;
    }
  }

  &:hover {
    svg {
      use {
        stroke: var(--second-color-hover);
      }
    }
  }

  &.prev {
    transform: translate(-100%, -50%);
    left: 0;

    svg {
      transform: rotate(180deg);
    }
  }

  &.next {
    transform: translate(100%, -50%);
    right: 0;
  }

  &.swiper-button-disabled {
    display: none;
  }
}



.often-order {
  width: 100%;
  height: 100%;

  .swiper-wrapper {
    // Это для box-shadow на додо такие же цифры
    padding: 0 10px;
    padding-top: 10px;
    padding-bottom: 20px;

  }
}

.swiper__often-order{
  overflow: visible;
}

.often-order-text{
  font-weight: 500;
  font-size: 24px;
  line-height: 1.4;
}

.often-order__slide {
  display: flex;
  gap: 10px;
  align-items: center;
  padding: 16px;
  min-width: 200px;
  max-width: 300px;
  box-shadow: rgba(6, 5, 50, 0.15) 0px 4px 22px -6px;
}

.often-order__slide-img {
  width: calc(1/3 * 100%);
  height: auto;
}

.often-order__slide-text {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.often-order__name {
  font-weight: 600;
  font-size: 18px;
  line-height: 20px;
}

.often-order__cost {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  position: relative;

  &.old {
    span {
      position: relative;
      display: inline-block;
    }

    span::before {
      content: '';
      position: absolute;
      width: 100%;
      height: 2px;
      background-color: red;
      top: 75%;
      left: 0;
      transform: rotate(-15deg);
      transform-origin: 0 100%;
    }

  }
}

.mobile__input-container {
  display: flex;
  gap: 5px;
  border: 1px solid rgba(92, 99, 112, 0.32);
  align-items: center;
  padding: 0 5px;
  border-radius: 8px;
  transition: 0.25s;
  width: 100%;
  background-color: rgb(69, 69, 69);

  &:hover {
    border-color: var(--second-color-hover);
  }

  &:has(.mobile__select-sity_input:focus) {
    border-color: var(--second-color-hover);
  }
}

.mobile__sityes-list-container {
  position: relative;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;
  margin: 0 calc(var(--container-offset) * -1);
}

/* Верхняя тень */
.mobile__sityes-list-container::before {
  content: "";
  position: absolute;
  /* Позиционирование внутри контейнера */
  top: 0;
  left: 0;
  right: 0;
  height: 60px;
  /* Высота тени */
  background: linear-gradient(to bottom, rgb(27, 27, 27, 1), rgb(27, 27, 27, 0));
  pointer-events: none;
  opacity: var(--top-shadow-opacity, 0);
  /* Управление видимостью через CSS переменную */
  transition: opacity 0.3s;
  z-index: 100;
}

/* Нижняя тень */
.mobile__sityes-list-container::after {
  content: "";
  position: absolute;
  /* Позиционирование внутри контейнера */
  bottom: 0;
  left: 0;
  right: calc(var(--container-offset) * -1);
  height: 60px;
  width: 110%;
  /* Высота тени */
  background: linear-gradient(to top, rgb(27, 27, 27, 1), rgb(27, 27, 27, 0));
  pointer-events: none;
  opacity: var(--bottom-shadow-opacity, 0);
  /* Управление видимостью через CSS переменную */
  transition: opacity 0.3s;
  z-index: 100;
}

.mobile__popup-sityes-list{
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  flex-grow: 1;
  padding: 0 var(--container-offset);
}

.search-icon {
  width: 24px;
  display: flex;
  align-items: center;
}

.mobile__select-sity_input {
  border: none;
  outline: none;
  font-size: 16px;
  line-height: 40px;
  font-weight: 500;
  width: 100%;
  min-width: 0;
  background-color: rgb(69, 69, 69);
}

.mobile__main-sityes{
  display: flex;
  flex-direction: column
}

.mobile__main-sity{
  padding: 10px 0px 10px 25px;
  font-size: 18px;
  font-weight: 500;
}

.mobile__sityes-group{
  position: relative;
}

.mobile__letter__sityes{
  position: absolute;
  padding: 10px 0;
  left: 2px;
  font-size: 18px;
  font-weight: 500;
}

.mobile__sity-name{
  padding: 10px 0px 10px 25px;
  font-size: 18px;
  font-weight: 500;
}


@media (max-width:1370px) {
  .storyboard-btn {
    display: none;
  }
}

@media (max-width:978px) {
  .mobile__header {
    display: flex;
  }

  .mobile__menu {
    position: absolute;
    display: flex;


    .mobile__select-sity-menu{
      display: flex;
      flex-direction: column;
      position: absolute;
      z-index: 25;
      top: 0;
      left: 0;
      color: #fff;
      gap: 20px;
      width: 100%;
      padding: 10px var(--container-offset);
      padding-bottom: 0;
      background-color: rgb(27, 27, 27);
      height: 100%;
      transition: 0.25s;
      transform: translateY(-100%);

      &.select-sity-menu__active{
        transform: translateY(0);
      }
    }

    &.menu--active {
      z-index: 20;
      transform: translateY(0);
      width: 100%;
      height: calc(100vh - var(--header-height));
      top: var(--header-height);
      flex-direction: column;
      background-color: #000;
      margin: 0 calc(var(--container-offset) * -1);
      padding-top: 16px;
      padding-bottom: 100px;
      padding-left: 35px;
      padding-right: var(--container-offset);
      border-top: 1px solid rgb(243, 243, 247);


      &.select-sity-menu__active{
        border-top: none;
      }

      .header__logo,
      .header__phone-number,
      .header__delivery-text,
      .header__phone-number {
        display: none;
      }

      .header__main-block {
        flex-direction: column;
        align-items: flex-start;
        gap: 16px;
      }

      .google-play__img {
        display: block;
        width: 200px;
      }

      .head__links {
        display: flex;
        flex-direction: column;

        .head__link {
          color: #fff;
        }
      }

      .mobile__delivery {
        display: flex;
        align-items: center;
        position: relative;

        .delivery-text {
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          gap: 3px;


          .delivery-adress {
            font-size: 20px;
            letter-spacing: 0.4px;
            font-weight: 400;
            color: #fff;
          }

          .delivery__subtext {
            font-size: 13px;
            line-height: 1;
            font-weight: 400;
            color: #fff;
            opacity: 0.5;
          }
        }

        .delivery-img {
          width: 24px;
          display: flex;
          align-items: center;
          position: absolute;
          left: -30px;
        }

      }

      .mobile__phone-number {
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        padding-top: 20px;

        .phone-text {
          display: flex;
          flex-direction: column;
          gap: 3px;


          .phone-number {
            font-size: 20px;
            letter-spacing: 0.4px;
            font-weight: 400;
            color: #fff;
          }

          .phone__subtext {
            font-size: 13px;
            line-height: 1;
            font-weight: 400;
            color: #fff;
            opacity: 0.5;
          }
        }

        .phone-img {
          width: 24px;
          display: flex;
          align-items: center;
          position: absolute;
          left: -30px;
        }
      }
    }
  }

  .mobile-delivery-time {
    display: flex;
    color: #fff;
    padding: 20px 0;
    align-items: center;
    gap: 10px;
    border-bottom: 1px solid rgb(243, 243, 247);
  }
}
