.delivery {
  width: 100%;
  margin: 16px 0;
}

.delivery__container {
  display: flex;
  flex-direction: column;
  gap: 32px;
}

.delivery-title {
  font-weight: 600;
  font-size: 36px;
  line-height: 1.1;
}

.delivery__content {
  display: flex;
  gap: 60px;

  &>* {
    flex: 1;
  }
}

.delivery-block {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.delivery-block__title {
  font-weight: 500;
  font-size: 18px;
  color: var(--second-color);
}

.delivery-block__description {
  font-weight: 500;
  font-size: 16px;
}

.delivery-img{
  width: 100%;
  aspect-ratio: 1/1;
  position: relative;
  overflow: hidden;

  &:hover{
    img{
      transform: scale(1.5);
    }
  }
  img{
    position: absolute;
    object-fit: cover;
    inset: 0px;
    transition: 10s;
  }
}


@media (max-width:768px) {
  .delivery__content{
    flex-direction: column;
    gap: 30px;
  }
}
