@charset "UTF-8";
:root {
  --font-family: "Inter", sans-serif;
  --content-width: 1280px;
  --container-offset: 20px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --header-height: 0px;
  --light-color: #fff;
  --second-color: rgb(0, 89, 255);
  --second-color-hover: rgb(0, 43, 232);
  --second-color-alpha: rgb(232, 230, 255);
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.page {
  height: 100%;
  font-family: var(--font-family);
  -webkit-text-size-adjust: 100%;
}

h1,
h2,
h3,
h4 {
  margin: 0;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden;
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: transparent;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

picture {
  display: flex;
}

.main {
  width: 100%;
  flex: 1 1 auto;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
}

.img-contain {
  width: 100%;
  height: auto;
  object-fit: contain;
  object-position: center;
}

.img-absolute {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
  object-position: center;
}

body {
  --width-active-bullet: 0%;
}

.header {
  width: 100%;
  position: relative;
}

.header__head {
  display: flex;
  flex-direction: column;
}

.head__links {
  display: flex;
  gap: 20px;
  padding: 12px 0;
}

.head__link {
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  transition: 0.25s;
  color: #000;
}
.head__link:hover {
  color: var(--second-color);
}

.header__head-border-bottom {
  width: 200%;
  transform: translateX(-25%);
  border-bottom: 1px solid rgb(243, 243, 247);
}

.header__container {
  display: flex;
  flex-direction: column;
  padding-bottom: 25px;
  gap: 16px;
}

.header__main-block {
  display: flex;
  gap: 40px;
  align-items: center;
}

.header__logo {
  width: 250px;
}

.header__delivery-text {
  font-size: 18px;
  line-height: 1.2;
  font-weight: 500;
}
.header__delivery-text .select-sity {
  color: var(--second-color);
  transition: 0.25s;
}
.header__delivery-text .select-sity:hover {
  color: var(--second-color-hover);
}

.header__phone-number {
  font-size: 18px;
  line-height: 1.2;
  font-weight: 500;
  color: #000;
  transition: 0.25s;
}
.header__phone-number:hover {
  color: var(--second-color);
}

.mobile__header {
  display: none;
  z-index: 25;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  transition: background-color 0.25s;
  padding: 12px 0;
}
.mobile__header.header--active {
  background-color: #000;
  padding: 12px var(--container-offset);
  margin: 0 calc(var(--container-offset) * -1);
}
.mobile__header.select-sity-menu__active {
  background-color: rgb(27, 27, 27);
}

.header__logo-mobile {
  width: 200px;
}

.mobile__phone-number,
.mobile__delivery,
.mobile-delivery-time,
.google-play__img,
.mobile__select-sity-menu {
  display: none;
}

.star-rating {
  display: inline-flex;
  direction: ltr;
  user-select: none;
}

.star {
  width: 16px;
  height: 16px;
  cursor: pointer;
  background: linear-gradient(to right, #fc0 0%, #fc0 100%, #ccc 100%, #ccc 100%);
  -webkit-mask: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><polygon points="12,2 15,9 22,9 17,14 19,21 12,17 5,21 7,14 2,9 9,9"/></svg>') center/contain no-repeat;
  mask: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><polygon points="12,2 15,9 22,9 17,14 19,21 12,17 5,21 7,14 2,9 9,9"/></svg>') center/contain no-repeat;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  transition: background 0.3s;
}

.delivery-time__text {
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
}

.delivery-time__rate {
  font-size: 18px;
  line-height: 20px;
  font-weight: 500;
  color: yellow;
}

.mobile__menu {
  display: contents;
  transform: translateY(-200%);
  transition: 0.25s;
}

.burger {
  --burger-width: 24px;
  --burger-height: 24px;
  --burger-line-height: 2px;
  position: relative;
  border: none;
  padding: 0;
  width: var(--burger-width);
  height: var(--burger-height);
  color: #000;
  background-color: transparent;
  margin-left: auto;
  cursor: pointer;
}
.burger::before, .burger::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger::before {
  top: 0;
}
.burger::after {
  top: calc(100% - var(--burger-line-height));
}
.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transform: translateY(-50%);
  transition: transform 0.3s ease-in-out;
}
.burger--active {
  color: #fff;
}
.burger--active::before {
  top: 50%;
  transform: rotate(45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active::after {
  top: 50%;
  transform: rotate(-45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active .burger__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}

.header__order {
  color: #fff;
  padding: 8px 20px;
  font-size: 16px;
  line-height: 24px;
  background-color: var(--second-color);
  transition: 0.25s;
  border-radius: 100px;
  margin-left: auto;
}
.header__order:hover {
  background-color: var(--second-color-hover);
}

.nav-logo {
  width: 0;
  transition: 0.25s;
}

.header-fixed {
  width: 100%;
}

.fixed {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 95;
  backdrop-filter: blur(10px);
  box-shadow: rgba(0, 0, 0, 0.1) 0px 2px 4px 0px;
  display: flex;
  justify-content: center;
}
.fixed .nav-logo {
  width: 32px;
}
.fixed .nav__container {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.sticky-placeholder {
  height: 0;
  visibility: hidden;
}

.nav-menu-line {
  width: 100%;
  display: flex;
  gap: 20px;
  align-items: center;
}

.nav-list {
  list-style: none;
  display: flex;
  gap: 20px;
  overflow-x: scroll;
  padding: 10px;
  align-items: center;
  scrollbar-width: none;
}

.nav-list__link {
  font-weight: 600;
  color: #000;
  font-size: 14px;
  transition: 0.25s;
}
.nav-list__link:hover {
  color: var(--second-color);
}

.storyboard {
  width: 100%;
  position: relative;
}

.swiper__storyboard {
  width: 100%;
  height: 100%;
  overflow: visible;
}

.storyboard__slide {
  min-width: 200px;
  width: auto;
  border-radius: 20px;
  overflow: hidden;
  cursor: pointer;
}

.storyboard-bg {
  width: 100%;
  aspect-ratio: 4/5;
  position: relative;
}
.storyboard-bg img {
  position: absolute;
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: center center;
}

.storyboard-btn {
  position: absolute;
  top: 50%;
  height: 48px;
  width: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.storyboard-btn svg {
  height: 24px;
  width: 14px;
  object-fit: contain;
}
.storyboard-btn svg use {
  fill: none;
  stroke: var(--second-color);
  stroke-linecap: round;
  stroke-width: 2px;
  stroke-linejoin: round;
  transition: 0.25s;
}
.storyboard-btn:hover svg use {
  stroke: var(--second-color-hover);
}
.storyboard-btn.prev {
  transform: translate(-100%, -50%);
  left: 0;
}
.storyboard-btn.prev svg {
  transform: rotate(180deg);
}
.storyboard-btn.next {
  transform: translate(100%, -50%);
  right: 0;
}
.storyboard-btn.swiper-button-disabled {
  display: none;
}

.often-order {
  width: 100%;
  height: 100%;
}
.often-order .swiper-wrapper {
  padding: 0 10px;
  padding-top: 10px;
  padding-bottom: 20px;
}

.swiper__often-order {
  overflow: visible;
}

.often-order-text {
  font-weight: 500;
  font-size: 24px;
  line-height: 1.4;
}

.often-order__slide {
  display: flex;
  gap: 10px;
  align-items: center;
  padding: 16px;
  min-width: 200px;
  max-width: 300px;
  box-shadow: rgba(6, 5, 50, 0.15) 0px 4px 22px -6px;
}

.often-order__slide-img {
  width: 33.3333333333%;
  height: auto;
}

.often-order__slide-text {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.often-order__name {
  font-weight: 600;
  font-size: 18px;
  line-height: 20px;
}

.often-order__cost {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  position: relative;
}
.often-order__cost.old span {
  position: relative;
  display: inline-block;
}
.often-order__cost.old span::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: red;
  top: 75%;
  left: 0;
  transform: rotate(-15deg);
  transform-origin: 0 100%;
}

.mobile__input-container {
  display: flex;
  gap: 5px;
  border: 1px solid rgba(92, 99, 112, 0.32);
  align-items: center;
  padding: 0 5px;
  border-radius: 8px;
  transition: 0.25s;
  width: 100%;
  background-color: rgb(69, 69, 69);
}
.mobile__input-container:hover {
  border-color: var(--second-color-hover);
}
.mobile__input-container:has(.mobile__select-sity_input:focus) {
  border-color: var(--second-color-hover);
}

.mobile__sityes-list-container {
  position: relative;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;
  margin: 0 calc(var(--container-offset) * -1);
}

/* Верхняя тень */
.mobile__sityes-list-container::before {
  content: "";
  position: absolute;
  /* Позиционирование внутри контейнера */
  top: 0;
  left: 0;
  right: 0;
  height: 60px;
  /* Высота тени */
  background: linear-gradient(to bottom, rgb(27, 27, 27), rgba(27, 27, 27, 0));
  pointer-events: none;
  opacity: var(--top-shadow-opacity, 0);
  /* Управление видимостью через CSS переменную */
  transition: opacity 0.3s;
  z-index: 100;
}

/* Нижняя тень */
.mobile__sityes-list-container::after {
  content: "";
  position: absolute;
  /* Позиционирование внутри контейнера */
  bottom: 0;
  left: 0;
  right: calc(var(--container-offset) * -1);
  height: 60px;
  width: 110%;
  /* Высота тени */
  background: linear-gradient(to top, rgb(27, 27, 27), rgba(27, 27, 27, 0));
  pointer-events: none;
  opacity: var(--bottom-shadow-opacity, 0);
  /* Управление видимостью через CSS переменную */
  transition: opacity 0.3s;
  z-index: 100;
}

.mobile__popup-sityes-list {
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  flex-grow: 1;
  padding: 0 var(--container-offset);
}

.search-icon {
  width: 24px;
  display: flex;
  align-items: center;
}

.mobile__select-sity_input {
  border: none;
  outline: none;
  font-size: 16px;
  line-height: 40px;
  font-weight: 500;
  width: 100%;
  min-width: 0;
  background-color: rgb(69, 69, 69);
}

.mobile__main-sityes {
  display: flex;
  flex-direction: column;
}

.mobile__main-sity {
  padding: 10px 0px 10px 25px;
  font-size: 18px;
  font-weight: 500;
}

.mobile__sityes-group {
  position: relative;
}

.mobile__letter__sityes {
  position: absolute;
  padding: 10px 0;
  left: 2px;
  font-size: 18px;
  font-weight: 500;
}

.mobile__sity-name {
  padding: 10px 0px 10px 25px;
  font-size: 18px;
  font-weight: 500;
}

@media (max-width: 1370px) {
  .storyboard-btn {
    display: none;
  }
}
@media (max-width: 978px) {
  .mobile__header {
    display: flex;
  }
  .mobile__menu {
    position: absolute;
    display: flex;
  }
  .mobile__menu .mobile__select-sity-menu {
    display: flex;
    flex-direction: column;
    position: absolute;
    z-index: 25;
    top: 0;
    left: 0;
    color: #fff;
    gap: 20px;
    width: 100%;
    padding: 10px var(--container-offset);
    padding-bottom: 0;
    background-color: rgb(27, 27, 27);
    height: 100%;
    transition: 0.25s;
    transform: translateY(-100%);
  }
  .mobile__menu .mobile__select-sity-menu.select-sity-menu__active {
    transform: translateY(0);
  }
  .mobile__menu.menu--active {
    z-index: 20;
    transform: translateY(0);
    width: 100%;
    height: calc(100vh - var(--header-height));
    top: var(--header-height);
    flex-direction: column;
    background-color: #000;
    margin: 0 calc(var(--container-offset) * -1);
    padding-top: 16px;
    padding-bottom: 100px;
    padding-left: 35px;
    padding-right: var(--container-offset);
    border-top: 1px solid rgb(243, 243, 247);
  }
  .mobile__menu.menu--active.select-sity-menu__active {
    border-top: none;
  }
  .mobile__menu.menu--active .header__logo,
  .mobile__menu.menu--active .header__phone-number,
  .mobile__menu.menu--active .header__delivery-text,
  .mobile__menu.menu--active .header__phone-number {
    display: none;
  }
  .mobile__menu.menu--active .header__main-block {
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
  }
  .mobile__menu.menu--active .google-play__img {
    display: block;
    width: 200px;
  }
  .mobile__menu.menu--active .head__links {
    display: flex;
    flex-direction: column;
  }
  .mobile__menu.menu--active .head__links .head__link {
    color: #fff;
  }
  .mobile__menu.menu--active .mobile__delivery {
    display: flex;
    align-items: center;
    position: relative;
  }
  .mobile__menu.menu--active .mobile__delivery .delivery-text {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 3px;
  }
  .mobile__menu.menu--active .mobile__delivery .delivery-text .delivery-adress {
    font-size: 20px;
    letter-spacing: 0.4px;
    font-weight: 400;
    color: #fff;
  }
  .mobile__menu.menu--active .mobile__delivery .delivery-text .delivery__subtext {
    font-size: 13px;
    line-height: 1;
    font-weight: 400;
    color: #fff;
    opacity: 0.5;
  }
  .mobile__menu.menu--active .mobile__delivery .delivery-img {
    width: 24px;
    display: flex;
    align-items: center;
    position: absolute;
    left: -30px;
  }
  .mobile__menu.menu--active .mobile__phone-number {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    padding-top: 20px;
  }
  .mobile__menu.menu--active .mobile__phone-number .phone-text {
    display: flex;
    flex-direction: column;
    gap: 3px;
  }
  .mobile__menu.menu--active .mobile__phone-number .phone-text .phone-number {
    font-size: 20px;
    letter-spacing: 0.4px;
    font-weight: 400;
    color: #fff;
  }
  .mobile__menu.menu--active .mobile__phone-number .phone-text .phone__subtext {
    font-size: 13px;
    line-height: 1;
    font-weight: 400;
    color: #fff;
    opacity: 0.5;
  }
  .mobile__menu.menu--active .mobile__phone-number .phone-img {
    width: 24px;
    display: flex;
    align-items: center;
    position: absolute;
    left: -30px;
  }
  .mobile-delivery-time {
    display: flex;
    color: #fff;
    padding: 20px 0;
    align-items: center;
    gap: 10px;
    border-bottom: 1px solid rgb(243, 243, 247);
  }
}
body.lock {
  overflow: hidden;
}

.storyes {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  overflow: hidden;
  transition: all 0.2s ease 0s;
  z-index: 100;
}

.storyes::after {
  content: "";
  position: absolute;
  top: -40px;
  left: -40px;
  right: -40px;
  bottom: -40px;
  background: inherit;
  filter: blur(30px);
  z-index: -1;
  overflow: hidden;
}

.storyes.open {
  opacity: 1;
  visibility: visible;
}

.storyes.open .popup__content {
  opacity: 1;
}

.storyes .popup__body {
  min-height: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.storyes .popup__content {
  position: relative;
  opacity: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 20px;
}

.story-btn {
  position: absolute;
  top: 50%;
  height: 48px;
  width: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.story-btn svg {
  height: 32px;
  width: 32px;
  object-fit: contain;
}
.story-btn svg use {
  fill: none;
  stroke: #fff;
  transition: 0.25s;
}
.story-btn:hover svg use {
  stroke: var(--second-color);
}
.story-btn.prev {
  transform: translate(-100%, -50%);
  left: 20%;
}
.story-btn.prev svg {
  transform: rotate(180deg);
}
.story-btn.next {
  transform: translate(100%, -50%);
  right: 20%;
}
.story-btn.swiper-button-disabled {
  display: none;
}

.swiper__story {
  max-width: 500px;
  height: 100vh;
  aspect-ratio: 1/1.58;
  padding: 25px 0;
  position: relative;
  object-fit: contain;
  display: flex;
  align-items: center;
}
.swiper__story .swiper-wrapper {
  height: auto;
}

.btn-close-container {
  position: absolute;
  width: 100%;
  padding: 25px 20px;
  aspect-ratio: 1/1.58;
}

.slide__story {
  display: flex;
  border-radius: 15px;
  overflow: hidden;
  position: relative;
  height: 100%;
  aspect-ratio: 1/1.58;
}

.close-storyboard {
  position: absolute;
  top: 5.5%;
  right: 2%;
  z-index: 15;
}

.swiper-button-lock {
  display: block;
}

.swiper__story-slide {
  width: 100%;
  height: 100%;
}

.story-fragment-btn {
  z-index: 10;
  position: absolute;
  width: 50%;
  height: 100%;
  top: 0;
}
.story-fragment-btn.prev {
  left: 0;
}
.story-fragment-btn.next {
  right: 0;
}

.slide__story-fragment {
  height: 100%;
  aspect-ratio: 1/1.58;
}
.slide__story-fragment img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.slide__story-fragment video {
  width: 100%;
  height: 100%;
  object-fit: contain;
  background-color: #000;
}

.pagination-fragments {
  position: absolute;
  top: 2% !important;
  right: 2%;
  left: 2% !important;
  z-index: 15;
  height: 10%;
  display: flex;
  width: unset !important;
}
