  background-color: silver !important;
  border-radius: 20px;
  opacity: 0.8;
  position: relative;
  height: 10%;
}

.swiper-pagination-bullet-active::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background-color: #fff;
  border-radius: 20px;
  height: 100%;
  z-index: 20;
  width: var(--width-active-bullet, 0%);
}

