.sityes-list-container {
  position: relative;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;
}

/* Верхняя тень */
.sityes-list-container::before {
  content: "";
  position: absolute;
  /* Позиционирование внутри контейнера */
  top: 0;
  left: 0;
  right: 0;
  height: 60px;
  /* Высота тени */
  background: linear-gradient(to bottom, rgb(255, 255, 255), rgba(255, 255, 255, 0));
  pointer-events: none;
  opacity: var(--top-shadow-opacity, 0);
  /* Управление видимостью через CSS переменную */
  transition: opacity 0.3s;
  z-index: 100;
}

/* Нижняя тень */
.sityes-list-container::after {
  content: "";
  position: absolute;
  /* Позиционирование внутри контейнера */
  bottom: 0;
  left: 0;
  right: 0;
  height: 60px;
  /* Высота тени */
  background: linear-gradient(to top, rgb(255, 255, 255), rgba(255, 255, 255, 0));
  pointer-events: none;
  opacity: var(--bottom-shadow-opacity, 0);
  /* Управление видимостью через CSS переменную */
  transition: opacity 0.3s;
  z-index: 100;
}

.popup-sityes-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  overflow-y: scroll;
  flex-grow: 1;
}

.column_sityes {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.sityes-group {
  position: relative;
  display: flex;
  flex-direction: column;
}

.letter__sityes {
  position: absolute;
  top: 5px;
  left: 5px;
  color: var(--second-color);
  font-size: 16px;
  font-weight: 400;
}

.sity-name {
  font-size: 16px;
  font-weight: 600;
  padding: 6px 30px;
  width: 100%;
  text-align: start;
}

.btn-popup-close {
  position: relative;
  top: 15px;
  right: 15px;
  width: 25px;
  height: 25px;
  cursor: pointer;
  border: none;
  background-color: transparent;
  transition: 0.25s;
}

.btn-container {
  position: absolute;
  top: -30px;
  right: 0;
  transform: translate(200%, 50%);
}

.btn-popup-close:hover::before,
.btn-popup-close:hover::after {
  width: 30px;
}

.btn-popup-close::before,
.btn-popup-close::after {
  content: "";
  position: absolute;
  top: 10px;
  display: block;
  width: 25px;
  height: 3px;
  background: #fff;
  transition: 0.25s;
}

.btn-popup-close::before {
  transform: rotate(45deg);
}

.btn-popup-close::after {
  transform: rotate(-45deg);
}

