body.lock {
  overflow: hidden;
}

.popup_select-sity {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  overflow-x: hidden;
  overflow-y: scroll;
  transition: all 0.2s ease 0s;
  z-index: 100;
  background-color: rgb(0, 0, 0, 0.5);
}

.popup_select-sity.open {
  opacity: 1;
  visibility: visible;
}

.popup_select-sity.open .popup__content {
  opacity: 1;
}

.popup_select-sity .popup_select-sity-body {
  min-height: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.popup_select-sity .popup_select-sity-content {
  position: relative;
  opacity: 0;
  display: flex;
  flex-direction: column;
  gap: 25px;
  background-color: #fff;
  padding: 30px;
  border-radius: 20px;
  max-height: 75%;
  height: 100%;
  width: 75%;
}

.popup__head {
  display: flex;
  gap: 10px;
}

.popup__logo {
  width: 50px;
  display: flex;
  align-items: center;
}

.popup__title {
  font-weight: 500;
  font-size: 36px;
}

.input-container {
  display: flex;
  gap: 5px;
  border: 1px solid rgba(92, 99, 112, 0.32);
  align-items: center;
  padding: 0 5px;
  border-radius: 8px;
  transition: 0.25s;
  width: 33%;

  &:hover {
    border-color: var(--second-color-hover);
  }

  &:has(.select-sity_input:focus) {
    border-color: var(--second-color-hover);
  }
}

.search-icon {
  width: 24px;
  display: flex;
  align-items: center;
}

.select-sity_input {
  border: none;
  outline: none;
  font-size: 16px;
  line-height: 40px;
  font-weight: 500;
  width: 100%;
  min-width: 0;
}

.main-sityes {
  display: flex;
  gap: 20px;
}

.main-sity {
  font-weight: 500;
  font-size: 22px;
  transition: 0.25s;

  &:hover {
    color: var(--second-color-hover);
  }
}

.sityes-list-container {
  position: relative;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;
}

/* Верхняя тень */
.sityes-list-container::before {
  content: "";
  position: absolute;
  /* Позиционирование внутри контейнера */
  top: 0;
  left: 0;
  right: 0;
  height: 60px;
  /* Высота тени */
  background: linear-gradient(to bottom, rgba(255, 255, 255, 1), rgba(255, 255, 255, 0));
  pointer-events: none;
  opacity: var(--top-shadow-opacity, 0);
  /* Управление видимостью через CSS переменную */
  transition: opacity 0.3s;
  z-index: 100;
}

/* Нижняя тень */
.sityes-list-container::after {
  content: "";
  position: absolute;
  /* Позиционирование внутри контейнера */
  bottom: 0;
  left: 0;
  right: 0;
  height: 60px;
  /* Высота тени */
  background: linear-gradient(to top, rgba(255, 255, 255, 1), rgba(255, 255, 255, 0));
  pointer-events: none;
  opacity: var(--bottom-shadow-opacity, 0);
  /* Управление видимостью через CSS переменную */
  transition: opacity 0.3s;
  z-index: 100;
}

.popup-sityes-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  overflow-y: scroll;
  flex-grow: 1;
}

.column_sityes {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.sityes-group {
  position: relative;
  display: flex;
  flex-direction: column;
}

.letter__sityes {
  position: absolute;
  top: 5px;
  left: 5px;
  color: var(--second-color);
  font-size: 16px;
  font-weight: 400;
}

.sity-name {
  font-size: 16px;
  font-weight: 600;
  padding: 6px 30px;
  width: 100%;
  text-align: start;
}



.btn-popup-close {
  position: relative;
  top: 15px;
  right: 15px;
  width: 25px;
  height: 25px;
  cursor: pointer;
  border: none;
  background-color: transparent;
  transition: 0.25s;
}

.btn-container {
  position: absolute;
  top: -30px;
  right: 0;
  transform: translate(200%, 50%);
}


.btn-popup-close:hover::before,
.btn-popup-close:hover::after {
  width: 30px;
}

.btn-popup-close::before,
.btn-popup-close::after {
  content: '';
  position: absolute;
  top: 10px;
  display: block;
  width: 25px;
  height: 3px;
  background: #fff;
  transition: 0.25s;
}

.btn-popup-close::before {
  transform: rotate(45deg);
}

.btn-popup-close::after {
  transform: rotate(-45deg);
}

